.course-detail {
  width: 100%;
}
.course-detail .detail-banner {
  width: 100%;
  height: 1.6rem;
  background: gray;
}
.course-detail .detail-banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.course-detail .tabs-detail .detail-t {
  border-bottom: 0.01rem solid #EEEEEE;
  padding: 0.16rem;
  width: 3.75rem;
  box-sizing: border-box;
}
.course-detail .tabs-detail .detail-t h2 {
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.12rem;
}
.course-detail .tabs-detail .detail-t p {
  line-height: 17px;
  color: #999999;
  font-size: 12px;
}
.course-detail .tabs-detail .detail-t p span {
  margin: 0 0.06rem;
}
.course-detail .tabs-detail .detail-c {
  border-bottom: 0.01rem solid #EEEEEE;
  padding: 0.16rem;
  width: 3.75rem;
  box-sizing: border-box;
}
.course-detail .tabs-detail .detail-c h3 {
  left: 0.29rem;
  height: 0.2rem;
  line-height: 0.2rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.13rem;
  text-indent: 0.13rem;
  border-left: 0.05rem solid #E9B551;
}
.course-detail .tabs-detail .detail-c p {
  line-height: 0.22rem;
  color: #333333;
  font-size: 0.14rem;
  margin-bottom: 0.16rem;
}
.course-detail .tabs-detail .detail-c .detail-c-b .left {
  width: 1.64rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #fff3d8;
  color: #dba131;
  font-size: 0.16rem;
  text-align: center;
  line-height: 0.4rem;
  margin-right: 0.15rem;
}
.course-detail .tabs-detail .detail-c .detail-c-b :last-child {
  margin-right: 0;
}
.course-detail .tabs-detail .detail-c .detail-c-b ::before {
  border: 0 !important;
}
.course-detail .detail-app .detail-app-i {
  width: 3.75rem;
  padding: 0.08rem 0.08rem 0.08rem 0.15rem;
  box-sizing: border-box;
  border-bottom: 1px solid #EEEEEE;
}
.course-detail .detail-app .detail-app-i .detail-app-i-l {
  width: 40%;
}
.course-detail .detail-app .detail-app-i .detail-app-i-l h3 {
  height: 0.22rem;
  color: #333333;
  font-size: 0.16rem;
  margin-bottom: 0.1rem;
}
.course-detail .detail-app .detail-app-i .detail-app-i-l h3 .am-badge-not-a-wrapper .am-badge-text {
  margin-left: 0.05rem;
  background: #dba131;
  min-width: 18px;
  border-radius: 50%;
  padding: 0;
}
.course-detail .detail-app .detail-app-i .detail-app-i-l p {
  line-height: 0.16rem;
  color: #999999;
  font-size: 0.12rem;
}
.course-detail .detail-app .detail-app-i .detail-app-i-m {
  width: 40%;
  text-align: right;
}
.course-detail .detail-app .detail-app-i .detail-app-i-m p {
  line-height: 0.16rem;
  color: #999999;
  font-size: 0.12rem;
}
.course-detail .detail-app .detail-app-i .detail-app-i-m .site-little {
  color: #FF3100;
}
.course-detail .detail-app .detail-app-i .detail-app-i-m :first-child {
  height: 0.22rem;
  padding-top: 0.05rem;
  margin-bottom: 0.1rem;
}
.course-detail .detail-app .detail-app-i .detail-app-i-r {
  width: 20%;
}
.course-detail .detail-app .detail-app-i .detail-app-i-r .btn {
  display: inline-block;
  width: 60px;
  height: 30px;
  border-radius: 4px;
  line-height: 30px;
  text-align: center;
  color: #dba131;
  background-color: #fff;
  margin-top: 0.06rem;
  float: right;
}
.course-detail .detail-app .detail-app-i .detail-app-i-r .btn.am-button-disabled {
  color: #999999;
  border: none;
}
.course-detail .detail-app .detail-app-i .detail-app-i-r .btn.am-button-disabled::before {
  border: 0.01rem solid #999999;
}
.course-detail .detail-app .detail-app-i .detail-app-i-r .btn span {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.sign-up {
  position: fixed !important;
  bottom: 0;
  height: 0.7rem;
  background: #fff;
  z-index: 15;
  width: 100%;
}
.sign-up .sign-up-btn {
  margin-left: 0.15rem;
  width: 3.43rem;
  height: 0.4rem;
  line-height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
}
