.index-advertising {
  position: relative;
}
.index-advertising .advertising-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  z-index: 11;
  background-color: rgba(0, 0, 0, 0.4);
}
.index-advertising .advertising-mask .advertising {
  width: 2.8rem;
  height: 3.8rem;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -1.4rem;
  margin-top: -1.9rem;
  border-radius: 0.1rem;
  background: #fff;
  z-index: 1000;
}
.index-advertising .advertising-mask .advertising .advertising-img {
  width: 100%;
  height: 100%;
  border-radius: 0.1rem;
}
.index-advertising .advertising-mask .advertising .advertising-close {
  width: 0.28rem;
  height: 0.28rem;
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: -0.14rem;
  margin-bottom: -0.4rem;
}
.foot-ad {
  width: 100%;
  box-sizing: border-box;
  padding: 0 0.15rem;
  border-top: 0.1rem solid #f5f5f5;
}
.foot-ad .title {
  height: 0.2rem;
  padding-top: 0.16rem;
  margin-bottom: 0.15rem;
  box-sizing: content-box;
}
.foot-ad .title span {
  line-height: 0.2rem;
  color: #c18b24;
  font-size: 0.16rem;
  text-align: left;
}
.foot-ad .title strong {
  height: 100%;
  width: 2.2rem;
  border-bottom: 0.02rem solid #e9b551;
  float: right;
}
.foot-ad .foot-ad-i {
  width: 100%;
  padding: 0.17rem 0;
  border-bottom: 0.01rem solid #FFEEEEEE;
}
.foot-ad .foot-ad-i .left {
  width: 1.8rem;
  margin-right: 0.24rem;
  text-align: center;
}
.foot-ad .foot-ad-i .left p {
  color: #333333;
  font-size: 0.16rem;
  text-align: center;
  margin-bottom: 0.16rem;
  font-weight: bolder;
}
.foot-ad .foot-ad-i .left span {
  display: inline-block;
  width: 0.64rem;
  height: 0.24rem;
  border-radius: 0.04rem;
  background-color: #fff3d8;
  color: #dba131;
  font-size: 0.12rem;
  text-align: center;
  line-height: 0.24rem;
}
.foot-ad .foot-ad-i .right {
  width: 1.4rem;
  height: 0.97rem;
}
.foot-ad .foot-ad-i .right img {
  width: 100%;
  height: 100%;
  background: #ddd;
}
.main-ad {
  width: 3.43rem;
  height: 1.51rem;
  padding: 0 0.16rem;
  position: relative;
  top: -0.3rem;
}
.main-ad img {
  width: 100%;
  height: 100%;
  border-radius: 0.05rem;
  background: gray;
}
